@charset "utf-8";
/* CSS Document */

/* ====================== Media screen ====================== */

@media screen and (min-width: 420px) {
  .filter-options {
    flex-wrap: nowrap;
  }
  .filter-options h2 {
    margin: auto 15px;
  }
}

@media screen and (min-width: 550px) {
  nav h1 a {
    font-size: 1.5em;
    letter-spacing: 5px;
  }
  #restaurants-list {
    display: flex;
    flex-flow: row wrap;
  }
  #restaurants-list li {
    width: calc((100% / 2) - 86px);
  }
  /* ============== Font =============== */
  #restaurants-list li h1,
  .restaurant-name_favorite {
    font-size: 2.4vw;
  }
  #restaurants-list li p {
    font-size: 2.2vw;
  }
  #footer {
    font-size: 1.5vw;
  }
  #restaurant-cuisine,
  #reviews-container h2 {
    font-size: 2.8vw;
  }
  #toast,
  #offline {
    font-size: 3.5vw;
  }
}

@media screen and (min-width: 700px) {
  /* ============== Font =============== */
  #restaurants-list li h1,
  .restaurant-name_favorite {
    font-size: 2.2vw;
  }
  #restaurants-list li p {
    font-size: 2vw;
  }
  #footer {
    font-size: 1.2vw;
  }
  #toast,
  #offline {
    font-size: 3vw;
  }
  #image-blurred-text h3 {
    font-size: 2vw
  }
  /* ==================================== */
}

@media screen and (min-width: 850px) {
  #maincontent {
    flex-direction: row;
    overflow-y: hidden;
  }
  #map-container {
    width: 40%;
    height: 100%;
  }
  .restaurants {
    overflow-y: scroll;
  }
  /* ============== Font =============== */
  #restaurants-list li h1,
  .restaurant-name_favorite {
    font-size: 1.5vw;
  }
  #restaurants-list li p {
    font-size: 1.3vw;
  }
  #footer {
    font-size: 1.1vw;
  }
  #toast,
  #offline {
    font-size: 2.7vw;
  }
  /* ==================================== */
  #restaurant-container {
    min-height: 50vh;
    width: 100%;
    overflow-y: scroll;
  }
}

@media screen and (min-width: 1280px) {
  #restaurants-list li {
    width: calc((100% / 3) - 86px);
  }
  /* ============== Font =============== */
  .filter-option {
    padding: 10px;
  }
  .filter-options h2 {
    font-size: 1.4vw;
    flex: 2;
  }
  .filter-options select {
    font-size: 1.2vw;
  }
  #footer {
    font-size: 0.8vw;
  }
  #toast,
  #offline {
    font-size: 2.4vw;
  }
  #restaurants-list li button.button {
    font-size: 1vw;
  }
  /* ==================================== */
  #restaurants-list li button.button {
    width: calc((1.2vw * 8) + 30px);
    left: calc(50% - ((1.2vw * 8)/2) - 15px)
  }
}

@media screen and (min-width: 1920px) {
  #restaurants-list li {
    width: calc((100% / 4) - 86px);
  }
  #toast,
  #offline {
    font-size: 1.8vw;
  }
}